import { toast } from "react-toastify";

//?------------------------------------------
// TODO: Toast code for ERROR purpose
//?------------------------------------------
export const ToastError = (message) =>
  toast.error(message, {
    position: "top-center",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: "dark",
  });

//?------------------------------------------
